.ct-form {
  margin: 5% 20%;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.ct-form form input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.ct-form form input[type=submit] {
  width: 100%;
  background-color: #c92651;
  color: white;
  padding: 14px 20px;
  margin: 15px 0 0 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.ct-form form input[type=submit]:hover {
  background-color: #c92651;
}

.ct-form label {
	display: block;
	margin: 30px 0 5px 0;
}

.ct-form select {
  width: 30%;
  float: right;
  margin: 0 0 0 0;
}

.input-message {
	display: block;
}


